	export default {
		// 外部公共组件
		components: {

	  	},
		//数据源
		data() {
			return {
				arr: [],
				mess:'',
				labelPosition: '',
				commonList: [ '手机短信', '平台短信'],
      			popShow: false,				//判断弹窗
      			isSelect1: false,			//判断手机短信选择
      			isSelect2:false,			//判断平台短信选择
      			
			}
		},
		// 生命周期
		mounted: function() {
			console.log(this.$route.query)
		},
		//行为
		methods: {

		    // 收件人
		    shouJianRen: function() {
		    	this.popShow = !this.popShow;
		    },
		    // 跳转选择联系人控件
		    goMailList: function() {
		    	this.$router.push({
					path: '/mailList',
					name: 'mailList',
					query: {
						'name': 'faduanxin'
					}
				})
		    },
		    // 判断手机短信选择
		    isSelects1: function() {
		    	this.isSelect1 = !this.isSelect1;
		    	if(this.isSelect1) {
		    		this.isSelect2 = false;
		    	}
		    },
		    // 判断平台短信函数
		    isSelects2: function() {
		    	this.isSelect2 = !this.isSelect2;
		    	if(this.isSelect2) {
		    		this.isSelect1 = false;
		    	}
		    }
		    
		}
		
	}



