export default {
	//数据源
	data() {
		return {
			search: false,
			inputVal: ''
		}
	},
	// 生命周期，跟随页面加载
	mounted: function() {

	},
	// 行为集合
	methods: {
		sousuo: function() {
			this.search = true
		},
		clear: function() {
			this.search = false;
			this.inputVal = '';
			this.change();
		},
		change: function() {
			this.$emit('searchData',this.inputVal);
		}
	}
}



