/* harmony default export */ var faWenXiangQing3 = ({

	name: 'faWenXiangQing',
	//引用外部组件
	components: {},
	//数据源
	data: function data() {
		return {
			wasDoDetails: {
				'formId': this.$cookie.getCookie('formId'),
				'nodeId': this.$cookie.getCookie('nodeId'),
				'isSignIn': this.$cookie.getCookie('isSignIn')
			},
			handleProcessInfoList: {
				'formId': this.$cookie.getCookie('formId')
			},
			content: '',
			bastInfo: {},
			fileList: [],
			// 流程
			liucheng: [],
			// 切换
			fawen: true,
			banli: false

		};
	},

	//跟随页面加载 只执行一次
	mounted: function mounted() {
		this.loadWasDoDetails();
		this.loadHandleProcessInfoList();
	},
	//行为
	methods: {
		//加载 wasDoInfo 接口数据
		loadWasDoDetails: function loadWasDoDetails() {
			var that = this;
			that.$http.post('api-office/office/sendfile/wasDoDetails', that.wasDoDetails, function (response) {
				var data = response.data.datamap;
				that.bastInfo = data.bastInfo;
				that.fileList = data.fileList;
				that.content = data.content;
			});
		},

		//加载 handleProcessInfoList 接口数据
		loadHandleProcessInfoList: function loadHandleProcessInfoList() {
			var that = this;
			this.$http.post('api-office/office/sendfile/handleProcessInfoList', this.handleProcessInfoList, function (response) {
				if (response.status === 200) {
					if (response.data.code === 1) {
						that.liucheng = response.data.list;
					}
				}
			});
		},

		//切换到办理流程，并加载办理流程数据
		Tab: function Tab() {
			var that = this;
			this.fawen = false;
			this.banli = true;
		},
		Tab2: function Tab2() {
			this.fawen = true;
			this.banli = false;
		},
		add0: function add0(m) {
			return m < 10 ? '0' + m : m;
		},

		//时间戳转化成时间格式
		timeFormat: function timeFormat(timestamp) {
			//timestamp是整数，否则要parseInt转换,不会出现少个0的情况
			var time = new Date(timestamp);
			var year = time.getFullYear();
			var month = time.getMonth() + 1;
			var date = time.getDate();
			var hours = time.getHours();
			var minutes = time.getMinutes();
			var seconds = time.getSeconds();
			return year + '-' + this.add0(month) + '-' + this.add0(date) + ' ' + this.add0(hours) + ':' + this.add0(minutes) + ':' + this.add0(seconds);
		}
	}
});
