export default {
	//数据源
	data() {
		return {
			
		}
	},
	props:['members'],
	// 生命周期，跟随页面加载
	mounted: function() {
	},
	// 行为集合
	methods: {
		userId: function(id) {
			this.$cookie.setCookie('userId',id);		//传用户id
			this.$router.push('memberInfo');	//跳转到用户信息页面
		},
		text: function(id) {
			alert('调用members子组件并传参数：'+id);
		}

	}
}



